<template>
  <el-row style="padding:16px">
    <el-col :span="24">
      <h3 class="cms-segmentation">关注人数统计</h3>
      <div>
        <el-radio-group v-model="type" size="mini" @change="changeData" style="margin-left:152px;">
          <el-radio-button :label="1">新增人数</el-radio-button>
          <el-radio-button :label="2">取消关注人数</el-radio-button>
          <el-radio-button :label="3">净增长人数</el-radio-button>
          <el-radio-button :label="4">累积人数</el-radio-button>
        </el-radio-group>
        <cms-picker v-model="timer" @change="getTimer"></cms-picker>
      </div>
      <div id="timeLine" class="charts" style="width:100%; height:400px;"></div>
    </el-col>
    <div class="stat__header mt-20">地域分布</div>
    <el-row class="stat__body">
      <el-col :sm="24" :xs="24" :md="24" :xl="12" :lg="12" class="stat__inner">
        <div id="pie" class="charts" style="height:400px;"></div>
      </el-col>
      <el-col :sm="24" :xs="24" :md="24" :xl="12" :lg="12" class="stat__inner border-left">
        <div id="map" class="charts" style="height:400px;"></div>
      </el-col>
    </el-row>

    <div class="stat__header mt-20">
     性别分布
    </div>
    <el-row class="stat__body">
      <el-col :span="12" class="stat__inner ">
        <div id="sex" class="charts" style="width:100%;height:150px;"></div>
      </el-col>
      <el-col :span="12" class="stat__inner border-left">
        <table class="stat-inner__table ">
          <tr>
            <th>性别</th>
            <th>数量</th>
          </tr>
          <tr v-for="item in sexOption.series" :key="item.name">
            <td>{{item.name}}</td>
            <td>{{item.data[0]}}</td>
          </tr>
        </table>
      </el-col>
    </el-row>
  <div class="stat__header mt-20">关注渠道分布</div>
   <el-row class="stat__body">
      <el-col :sm="24" :xs="24" :md="24" :xl="12" :lg="12" class="stat__inner  border-right">
       <div id="subscribe" class="charts" style="width:100%;height:300px;"></div>
      </el-col>
      <el-col :sm="24" :xs="24" :md="24" :xl="12" :lg="12" class="stat__inner ">
      <table class="stat-inner__table ">
          <tr>
            <th>来源</th>
            <th>数量</th>
          </tr>
          <tr v-for="item in subOption.tableData" :key="item.name">
            <td>{{item.name}}</td>
            <td>{{item.value}}</td>
          </tr>
        </table>
      </el-col>
    </el-row>
  </el-row>
</template>

<script>
import echarts from 'echarts'
import {
  weChatStatisticsGetFans,
  weChatStatisticsRegion,
  weChatStatisticsSex,
  weChatStatisticsSubscribe
} from '@/api/wechat/index.js'
export default {
  props: {
    appId: {
      type: String
    }
  },
  data() {
    return {
      pieChart: null,
      fansChart: null,
      mapChart: null,
      sexChart: null,
      subChart: null,
      params: {
        beginDate: this.$moment(
          new Date().getTime() - 86400000 * 7,
          'YYYY-MM-DD'
        ),
        endDate: this.$moment(new Date().getTime() - 86400000, 'YYYY-MM-DD')
      },
      type: 1,
      timer: [
        this.$moment(new Date().getTime() - 86400000 * 7, 'YYYY-MM-DD'),
        this.$moment(new Date().getTime() - 86400000, 'YYYY-MM-DD')
      ],
      dataItems: {
        newUser: [],
        netGrowthUser: [],
        countUser: [],
        cancelUser: []
      },

      fansOption: {
        tooltip: {
          trigger: 'axis',
          formatter: '{b} <br/>新增人数: {c} '
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              type: 'dashed'
            }
          }
        },
        series: [
          {
            data: [],
            type: 'line',
            areaStyle: {}
          }
        ]
      },
      pieOption: {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          type: 'scroll',
          orient: 'vertical',
          right: 'right',
          data: [],
          selected: []
        },
        series: [
          {
            name: '国家或地区',
            type: 'pie',
            radius: '55%',
            center: ['45%', '50%'],
            data: []
          }
        ]
      },
      mapOption: {
        tooltip: {
          trigger: 'item',
          formatter: function(params, ticket, callback) {
            let value = ''
            if (isNaN(params.value)) {
              value = 0
            } else {
              value = params.value
            }
            return params.name + '<br/>' + value
          }
        },
        visualMap: {
          realtime: false,
          calculable: true,
          left: '15%',
          inRange: {
            color: ['lightskyblue', 'yellow', 'orangered']
          }
        },
        series: [
          {
            name: '关注用户分布',
            type: 'map',
            map: 'china',
            itemStyle: {
              normal: { label: { show: true }},
              emphasis: { label: { show: true }}
            },
            data: []
          }
        ]
      },
      sexOption: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          data: ['男', '女'],
          top: 'bottom'
        },
        grid: {
          left: '0',
          right: '10%',
          bottom: '40%',
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          type: 'category',
          data: ['男女比例']
        },
        series: []
      },
      subOption: {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          right: 'right',
          bottom: '10%',
          data: []
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            center: ['40%', '50%'],
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '12',
                  fontWeight: 'bold'
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: []
          }
        ]
      }
    }
  },
  methods: {
    getTimer(value) {
      if (value.length > 0) {
        this.params.beginDate = value[0]
        this.params.endDate = value[1]
        this.getFansData(this.params)
      }
    },
    changeData(value) {
      if (value === 1) {
        this.fansOption.series[0].data = this.dataItems.newUser
        this.fansOption.tooltip.formatter = '{b} <br/>新增人数: {c} '
      }
      if (value === 2) {
        this.fansOption.series[0].data = this.dataItems.cancelUser
        this.fansOption.tooltip.formatter = '{b} <br/>取消关注人数: {c} '
      }
      if (value === 3) {
        this.fansOption.series[0].data = this.dataItems.netGrowthUser
        this.fansOption.tooltip.formatter = '{b} <br/>净增长人数: {c} '
      }
      if (value === 4) {
        this.fansOption.series[0].data = this.dataItems.countUser
        this.fansOption.tooltip.formatter = '{b} <br/>累积人数: {c} '
      }
      this.fansChart.setOption(this.fansOption)
    },
    getFansData(params) {
      // 获取关注数量
      const param = Object.assign({ appId: this.appId }, params)
      weChatStatisticsGetFans(param).then(res => {
        this.dataItems.newUser = res.data.map(v => v.newUser)
        this.dataItems.netGrowthUser = res.data.map(v => v.netGrowthUser)
        this.dataItems.countUser = res.data.map(v => v.countUser)
        this.dataItems.cancelUser = res.data.map(v => v.cancelUser)
        this.fansOption.xAxis.data = res.data.map(v => v.statisticsDate)
        this.changeData(this.type)
        this.fansChart.setOption(this.fansOption)
      })
    },
    getMapData() {
      // 获取地区统计
      weChatStatisticsRegion({ appId: this.appId }).then(res => {
        const selected = {}
        res.data.world.forEach((v, i) => {
          if (i < 5) {
            selected[v.name] = true
          } else {
            selected[v.name] = false
          }
        })
        this.mapChart.hideLoading()
        this.mapOption.series[0].data = res.data.china
        this.pieOption.series[0].data = res.data.world
        this.pieOption.legend.data = res.data.world
        this.pieOption.legend.selected = selected
        this.mapChart.setOption(this.mapOption)
        this.pieChart.setOption(this.pieOption)
      })
    },
    getSexData() {
      // 获取性别
      weChatStatisticsSex({ appId: this.appId }).then(res => {
        this.sexOption.legend.data = res.data.map(v => v.name)
        res.data.forEach((v, i) => {
          this.sexOption.series.push({
            name: v.name,
            type: 'bar',
            stack: '数量',
            data: [v.value]
          })
        })
        this.sexChart.setOption(this.sexOption)
      })
    },
    getSubscribeData() {
      weChatStatisticsSubscribe({ appId: this.appId }).then(res => {
        this.subOption.series[0].data = res.data
        this.subOption.tableData = res.data
        this.subOption.legend.data = res.data.map(v => v.name)
        this.subChart.setOption(this.subOption)
      })
    }
  },
  mounted() {
    const geoJson = require('./../../../../../node_modules/echarts/map/json/china.json')
    echarts.registerMap('china', geoJson)
    this.fansChart = echarts.init(
      document.getElementById('timeLine'),
      'walden'
    )
    this.mapChart = echarts.init(document.getElementById('map'), 'walden')
    this.pieChart = echarts.init(document.getElementById('pie'), 'walden')
    this.sexChart = echarts.init(document.getElementById('sex'), 'walden')
    this.subChart = echarts.init(
      document.getElementById('subscribe'),
      'walden'
    )
    this.mapChart.showLoading()
    this.getSexData()
    this.getSubscribeData()
    this.getFansData(this.params)
    this.getMapData()
    window.addEventListener('resize', () => {
      if (this.sexChart) {
        this.sexChart.resize()
      }
      if (this.subChart) {
        this.subChart.resize()
      }
      if (this.fansChart) {
        this.fansChart.resize()
      }
      if (this.mapChart) {
        this.mapChart.resize()
      }
      if (this.pieChart) {
        this.pieChart.resize()
      }
    })
  }
}
</script>

<style>
</style>



