<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/wechatPublic/list" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>系统接入公众号统一采用授权方式接入，无法进行修改信息</li>
      </cms-prompt>
      <el-form ref="form" :model="formData" label-width="210px" style="margin: 0 auto;">
        <div class="product-item-one">
          <el-row>
            <el-col style="margin-top: 7px;margin-left: 27px;width:95%">
              <div class="product-img">
                <img class="product-img-img" :src="formData.abstractWeChatInfo.headImg">
              </div>
              <div class="product-name">
                {{formData.abstractWeChatInfo.wechatName}}
              </div>
              <div class="product-attr">
                <span>类型：
                  <span class="jiacu" v-if="formData.abstractWeChatInfo.wechatType==1">
                    普通订阅号（含历史老账号升级后的订阅号）
                  </span>
                  <span class="jiacu" v-if="formData.abstractWeChatInfo.wechatType==2">
                    服务号
                  </span>
                  <i class="iconfont"></i>
                </span>
                <span class="li-span">|</span>
                <span>拥有者：
                  <span class="jiacu" v-if="this.formData.abstractWeChatInfo.owerType==1">
                    平台
                  </span>
                  <span class="jiacu" v-if="this.formData.abstractWeChatInfo.owerType==2">
                    商家
                  </span>
                  <i class="iconfont"></i>
                </span>
                <span class="li-span">|</span>
                <span style="margin-left:10px">
                  <i class="iconfont icon-gou success" style="color:green"></i>
                  <span class="jiacu" v-if="formData.abstractWeChatInfo.grantType=== 1">授权接入
                  </span>
                </span>
              </div>
            </el-col>
          </el-row>
        </div>
        <div class="account-statistics">
          <el-row>
            <el-col>
              <h3 class="cms-segmentation">今日关键指数</h3>
              <ul class="account-statistics__items">
                <li>
                  <div class="li-zong">
                    <div class="li-left-div">
                      <i class="iconfont icon-guanzhu"></i>
                      <p class="icon-guanzhu-p">新关注人数</p>
                    </div>
                    <div class="li-right-div">
                      <div>
                        <span class="today">今日：</span>
                        <span class="today-count">{{formData.fansStatistics.toDay.newUser}}</span>
                      </div>
                      <div>
                        <span class="yestaday">昨日：</span>
                        <span class="yestaday-count">{{formData.fansStatistics.yesterDay.newUser}}</span>
                      </div>
                    </div>
                    <span class="li-span">|</span>
                  </div>
                </li>

                <li>
                  <div class="li-zong">
                    <div class="li-left-div">
                      <i class="iconfont icon-quxiaoguanzhu"></i>
                      <p class="icon-quxiaoguanzhu-p">取消关注人数</p>
                    </div>
                    <div class="li-right-div">
                      <div>
                        <span class="today">今日：</span>
                        <span class="today-count">{{formData.fansStatistics.toDay.cancelUser}}</span>
                      </div>
                      <div>
                        <span class="yestaday">昨日：</span>
                        <span class="yestaday-count">{{formData.fansStatistics.yesterDay.cancelUser}}</span>
                      </div>
                    </div>
                    <span class="li-span">|</span>
                  </div>
                </li>
                <li>
                  <div class="li-zong">
                    <div class="li-left-div">
                      <i class="iconfont icon-xin"></i>
                      <p class="icon-xin-p">净关注人数</p>
                    </div>
                    <div class="li-right-div">
                      <div>
                        <span class="today">今日：</span>
                        <span class="today-count">{{formData.fansStatistics.toDay.netGrowthUser}}</span>
                      </div>
                      <div>
                        <span class="yestaday">昨日：</span>
                        <span class="yestaday-count">{{formData.fansStatistics.yesterDay.netGrowthUser}}</span>
                      </div>
                    </div>
                    <span class="li-span">|</span>
                  </div>
                </li>
                <li>
                  <div class="li-zong">
                    <div class="li-left-div">
                      <i class="iconfont icon-guanzhu1"></i>
                      <p class="icon-guanzhu1-p">累计关注人数</p>
                    </div>
                    <div class="li-right-div">
                      <div>
                        <span class="today">今日：</span>
                        <span class="today-count">{{formData.fansStatistics.toDay.countUser}}</span>
                      </div>
                      <div>
                        <span class="yestaday">昨日：</span>
                        <span class="yestaday-count">{{formData.fansStatistics.yesterDay.countUser}}</span>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </el-col>
          </el-row>
        </div>
      </el-form>
    </div>
    <!-- 地图容器 -->
     <time-charts :appId='$route.query.appId' v-if="status"></time-charts> 
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import { weChatStatistics } from '@/api/wechat/index.js'
import timeCharts from './timeCharts'
var defaultData = {
  abstractWeChatInfo: {
    grantType: '',
    headImg: '',
    owerType: '',
    wechatName: '',
    wechatType: ''
  },
  fansStatistics: {
    toDay: {
      newUser: '',
      cancelUser: '',
      netGrowthUser: '',
      countUser: '',
      updateUser: '',
      updateTime: '',
      createUser: '',
      createTime: '',
      appId: ''
    },
    yesterDay: {
      newUser: '',
      cancelUser: '',
      netGrowthUser: '',
      countUser: '',
      updateUser: '',
      updateTime: '',
      createUser: '',
      createTime: '',
      appId: ''
    }
  }
}
export default {
  name: 'publicSetting',
  components: {
    timeCharts
  },
  mixins: [form],
  data() {
    return {
      appId: '',
      status: false,
      yesterday: [],
      today: [],
      formData: Object.assign({}, defaultData)
    }
  },
  methods: {
    getFormData() {
      // 通过id获取表单信息
      weChatStatistics({ appId: this.appId }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    }
  },
  created() {
    this.appId = this.$route.query.appId
    this.status = true
    this.getFormData()
  }
}
</script>


<style scoped>
.product-img {
  float: left;
}
.product-p span {
  line-height: 30px;
}
.product-p {
  float: left;
  margin-right: 10px;
  line-height: 20px;
}
.product-desc {
  float: left;
  margin-left: 30px;
}
.product-name {
  margin-top: 30px;
  margin-left: 105px;
}
.product-attr {
  margin-top: 20px;
  margin-left: 105px;
}
.product-img-img {
  width: 80px;
  height: 80px;
  margin-top: 20px;
  border-radius: 12px;
  border: solid 2px #f4f8fb;
}
.account-statistics {
  width: 100%;
  margin-top: 20px;
}
.account-statistics__items {
  list-style: none;
  width: 100%;
  display: flex;
  justify-content: center;
}
.account-statistics__items li {
  float: left;
  margin-left: 15px;
  width: 300px;
  text-align: center;
  padding-top: 30px;
  font-size: 18px;
  height: 100px;
}
.li-left-div {
  float: left;
}
.icon-guanzhu {
  font-size: 30px;
  color: #fb951e;
}
.icon-guanzhu-p {
  font-size: 14px;
  color: #fb951e;
}
.icon-quxiaoguanzhu {
  color: #69b97e;
  font-size: 30px;
}
.icon-quxiaoguanzhu-p {
  color: #69b97e;
  font-size: 14px;
}
.icon-xin {
  color: #8cbdd9;
  font-size: 30px;
}
.icon-xin-p {
  color: #8cbdd9;
  font-size: 14px;
}
.icon-guanzhu1 {
  color: #ff7a70;
  font-size: 30px;
}
.icon-guanzhu1-p {
  color: #ff7a70;
  font-size: 14px;
}
.span-li {
  width: 1px;
  height: 24px;
  font-size: 6px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 20px;
  letter-spacing: 1px;
  color: #e7e7e7;
}
.li-right-div {
  margin-left: 24px;
  float: left;
}
.today {
  width: 33px;
  height: 14px;
  font-size: 14px;
  color: #444444;
}
.today-count {
  width: 22px;
  height: 15px;
  font-size: 20px;
  color: #444444;
}
.yestaday {
  width: 33px;
  height: 14px;
  font-size: 12px;
  color: #444444;
}
.yestaday-count {
  width: 22px;
  height: 15px;
  font-size: 12px;
  color: #444444;
}
.li-span {
  color: #e7e7ee;
  margin: 0 15px 0 15px;
}
/* -- */
.product-item-one {
  width: 100%;
  margin-top: 30px;
  height: 130px;
  background-color: #ffffff;
  border-radius: 12px;
  -moz-box-shadow: 2px 2px 10px #999999;
  -webkit-box-shadow: 2px 2px 10px #999999;
  box-shadow: 0px 2px 16px #999999;
}
.cms-layout .el-form-item {
  border-bottom: 1px solid #f7f7f7;
}
.product-img {
  float: left;
}
.product-p span {
  line-height: 30px;
}
.product-p {
  float: left;
  margin-top: 10px;
  width: 170px;
  margin-left: 5px;
  margin-right: 10px;
  line-height: 20px;
}
.product-desc {
  float: left;
  margin-left: 30px;
}
.product-btn {
  float: right;
  line-height: 110px;
}
.product-name {
  margin-top: 30px;
  height: 15px;
  font-size: 18px;
  line-height: 24px;
  color: #444444;
}
.green {
  color: #0000ff;
  cursor: pointer;
}
.li-zong {
  width: 225px;
}
.jiacu {
  font-size: 14px;
  color: #444444;
}
</style>



